import { Stack, Typography } from '@mui/material'
import React, { useDebugValue } from 'react'
import { useDispatch, useSelector } from 'react-redux'


import Icon from "../../assets/icons/gym.png"
import { setBodyPart } from '../../store/exerciseSlice'

const BodyPart = ({ item }) => {
    const bodyPart = useSelector(state => state.exercises.bodyPart)
    const dispatch = useDispatch()
    return (
        <Stack type="button" alignItems={"center"} justifyContent="center" className='bodyPart-card'
            sx={{
                borderTop: bodyPart === item && '4px solid #ff2625',
                background: '#fff',
                borderBottomLeftRadius: '20px',
                width: '270px',
                height: '280px',
                cursor: 'pointer',
                gap: "47px"
           }} onClick={ () => {
            dispatch(setBodyPart(item))
            window.scroll( {top: 1800, left: 100, behavior: 'smooth'})
           }}>
            <img src={Icon} alt="dumbbel" style={{ width: '60px', height: '60px' }} />

            <Typography fontSize={"24px"} fontWeight="bold" color="#3A1212">{item}</Typography>
        </Stack >
    )
}

export default BodyPart
