import React, { memo } from 'react'
import { Stack } from "@mui/material"
import { Link } from "react-router-dom"

import Logo from "../assets/images/Logo.png"


const Navbar = memo(() => {
  return (
    <Stack direction={"row"} justifyContent={"space-around"} sx={{
      gap: { sm: "122px", xc: "40px" }, mt: { sm: "32x", xc: "20px" },
      justifyContent: "none"
    }} px={"20px"}>

      <LinkHref img href='#img' to={"/"}
        styles={{ width: "48px", height: "48px", margin: "0 20px" }} />

      <Stack direction={"row"} gap={"40px"} fontSize={"20px"} alignItems={"flex-end"}>

        <LinkHref content='home' href='#home' to={"/"}
          styles={{ textDecoration: "none", color: "#3A1212", borderBottom: "3px solid #FF2625" }} />
        <LinkHref content={'exercise'} href='#exercises' to={"/exercises"}
          tyles={{ textDecoration: "none", color: "#3A1212", }} />

      </Stack>

    </Stack>
  )
})


const LinkHref = ({ href, to, styles, content, img }) => {
  return (
    <Link href={href} to={to}>
      {
        img ? (
          <img src={Logo} style={styles} />
        )
          : (<span style={styles}>
            {content}
          </span>
          )
      }
    </Link>
  )

}

export default Navbar
