import React, { useEffect, useState } from 'react'
import { Button, Checkbox, Pagination, Stack, Typography } from '@mui/material'
import { useDispatch, useSelector } from 'react-redux'

import { fetchData, exerciseOptions } from '../../utils/fetchData'
import { Box } from '@mui/system'
import ExerciseCard from '../exercise/ExrciseCard'
import { setExercises } from '../../store/exerciseSlice'

const Exercises = () => {
  const bodyPart = useSelector(state => state.exercises.bodyPart)
  const exercises = useSelector(state => state.exercises.exercises)
  const dispatch = useDispatch()


  const [currentPage, setCurrentPage] = useState(1)
  const [equipMode, setEquipMode] = useState(false)

  let withoutEquipmentExerciseArray = []


  const exercisesPerPage = 18

  const indexOfLastExercise = currentPage * exercisesPerPage
  const indexOfFirstExercise = indexOfLastExercise - exercisesPerPage

  const currentExercises = exercises.slice(indexOfFirstExercise, indexOfLastExercise)

  const paginate = (e, value) => {
    setCurrentPage(value)

    window.scrollTo({ top: 1800, behavio: 'smooth' })
  }

  useEffect(() => {
    const fetchExercisesData = async () => {
      let exerciseData = []

      if (bodyPart === "all") {
        exerciseData = await fetchData(`https://exercisedb.p.rapidapi.com/exercises`, exerciseOptions)
      } else {
        exerciseData = await fetchData(`https://exercisedb.p.rapidapi.com/exercises/bodyPart/${bodyPart}`, exerciseOptions)
      }

      !equipMode 
      ? dispatch(setExercises(exerciseData)) 
      : dispatch(setExercises(exerciseData.filter(element => element.equipment === "body weight")))
    }
    fetchExercisesData()
  }, [bodyPart, equipMode])

  return (
    <Box id='exercises'
      sx={{
        mt: { lg: '110px' }
      }}
      mt="50px"
      p="20px">
      {equipMode
        ? <Button onClick={() => setEquipMode(false)}>
          with equipment
        </Button>
        : <Button onClick={() => setEquipMode(true)}>
          without equipment
        </Button>
      }

      <Typography variant='h3' mb="46px">
        showing results
        {/* <input type={'checkbox'} value={checked} onChange={(e) => {setChecked(e.target.value);debugger}}/> */}
      </Typography>

      <Stack direction={"row"} sx={{ gap: { lg: '110px', xs: '50px' } }}
        flexWrap='wrap' justifyContent={"center"}>
        {currentExercises.map((exercise, index) => (
          <ExerciseCard key={index} exercise={exercise} />
        ))}
      </Stack>

      <Stack mt="100px" alignItems={"center"}>
        {exercises.length > exercisesPerPage && (
          <Pagination color='standard' shape='rounded' count={Math.ceil(exercises.length / exercisesPerPage)}
            page={currentPage}
            onChange={(e, value) => paginate(e, value)}
            size="large" />
        )}
      </Stack>
    </Box>
  )
}

export default Exercises
