import React, { useEffect, useState } from 'react'
import { Box, Button, Checkbox, Stack, TextField, Typography } from "@mui/material"
import { exerciseOptions, fetchData } from '../../utils/fetchData'
import HorizontalScrollbar from '../../common/HorizontalScrollbar'
import { useSelector, useDispatch } from 'react-redux'

import {setExercises} from '../../store/exerciseSlice'

const SearchExercise = () => {
  const dispatch = useDispatch()

  const [search, setSearch] = useState('')
  const [bodyParts, setBodyParts] = useState([])

  useEffect(() => {
    const fetchExercisesData = async () => {
      const bodyPartsData = await fetchData(`https://exercisedb.p.rapidapi.com/exercises/bodyPartList`, exerciseOptions)

      setBodyParts(['all', ...bodyPartsData])
    }
    fetchExercisesData()
  }, [])

  const handleSearch = async () => {
    if (search) {
      const exercisesData = await fetchData(`https://exercisedb.p.rapidapi.com/exercises`, exerciseOptions)
      const searchedExercises = exercisesData.filter(
        exercise => exercise.name.toLowerCase().includes(search)
          || exercise.bodyPart.toLowerCase().includes(search)
          || exercise.equipment.toLowerCase().includes(search)
          || exercise.target.toLowerCase().includes(search)
      )
      setSearch('')
      dispatch(setExercises(searchedExercises))
    }

  }

  return (
    <Stack alignItems={"center"} mt="37px" justifyContent={"center"} p="20px">
      <Typography fontWeight={700} sx={{
        fontSize: { lg: "44px", xs: "39px" }
      }} mb="50px" textAlign={"center"}>
        Awesome Exercise You <br />
        Should Know
      </Typography>

      <Box position={"relative"} mb="72px">
        <TextField height="76px" value={search} onChange={e => setSearch(e.target.value.toLocaleLowerCase())}
          placeholder="search" type="text"
          sx={{
            input: {
              fontWeight: '700',
              border: 'none',
              borderRadius: '4px'
            },
            width: { lg: '800px', xs: '350px' },
            background: '#fff',
            borderRadius: '40px'
          }} />

        <Button className='search-btn' sx={{
          bgcolor: '#FF2625',
          color: "#fff",
          textTransform: 'none',
          width: { lg: '175px', xs: '83px' },
          fontSize: { lg: '20px', xs: '14px' },
          height: '56px',
          position: 'absolute'
        }}
          onClick={handleSearch}>search</Button>

        <Checkbox
          sx={{
            '& .MuiSvgIcon-root': { fontSize: 28 },
            color: '#FF1493',
            '&.Mui-checked': { color: '#FF1493', }
          }} />
      </Box>



      <Box sx={{
        position: 'relative', width: '100%', p: '20px'
      }}>
        <HorizontalScrollbar isBodyPart data={bodyParts} />
      </Box>
    </Stack>
  )
}

export default SearchExercise
