import { Button, debounce, Stack, Typography } from '@mui/material'
import React from 'react'

import BodyPartImage from "../../assets/icons/body-part.png"
import TargetImage from "../../assets/icons/target.png"
import EquipmentImage from "../../assets/icons/equipment.png"

const Detail = ({ exerciseDetail }) => {

    const { bodyPart, gifUrl, name, target, equipment } = exerciseDetail

    const extraDetail = [
        {
            icon: BodyPartImage,
            name: bodyPart
        },
        {
            icon: TargetImage,
            name: target
        },
        {
            icon: EquipmentImage,
            name: equipment
        },
    ]
    return (
        <Stack gap='60px' alignItems={"center"} sx={{ flexDirection: { lg: 'row' }, p: '20px' }}>
            <img src={gifUrl} alt={name} loading="lazy" className='detail-image' />

            <Stack sx={{ gap: { lg: '35px', xs: '20px' }}}>
                <Typography variant="h3">
                    {name}
                </Typography>

                <Typography variant='h6'>
                    Exercises keep you strong. <b>{name + ' '}</b>is one of the best
                    exercises to target your <b>{target}</b>. It will help you improve your
                    mood and gain energy 
                </Typography>

                {extraDetail.map( item => (
                    <Stack key={item.name} direction="row" gap='24px' alignItems="center">
                        <Button sx={{ background: '#fff2db', borderRadius: '50%', width:'100px', height: '100px'}}>
                            <img style={{width:'50px', height:"50px"}} src={item.icon}/></Button>
                        <Typography textTransform={"capitalize"} variant="h5">{item.name}</Typography>
                    </Stack>
                ))}

            </Stack>
        </Stack>
    )
}

export default Detail
